import '../common/style/app.css';
import 'weui';
import weui from 'weui.js';


import { getUrl } from '../common/js/http.js';
// import '../common/js/oauth.js';
import '../common/js/tabbar.js';


$(function () {
	$('#fillout').on('click', function (e) {
		var url = getUrl('/fillout.html');
		window.location.replace(url);
	});

	$('#online-pay').on('click', function (e) {
		var url = getUrl('/pay.html');
		window.location.replace(url);
	});
	
})